.creator {
  width: 100%;
  height: 100%;
  text-align: center;

  .pizza_builder {
    display: grid;
    grid-template-rows: minmax(130px, auto) minmax(130px, auto) auto auto;
    grid-template-areas: "preview" "preview" "picker1" "picker2";
    min-height: 250px;

    .ingredients_picker_1 {

      span[id^=igd] {
        display: inline-block;
        font-size: .8rem;
        color: grey; }

      label {
        display: block;
        padding: .5rem .5rem 0 .5rem;

        span {
          color: black; } } }

    .ingredients_picker_2 {
      background-color: purple; }

    .pizza_preview {
      grid-area: preview;
      display: flex;
      justify-content: center;
      position: relative;

      .preview_item {
        position: absolute;
        align-self: center;
        width: 80%;
        height: auto;
        animation: fadeIn .6s ease-out; } } } }

@keyframes fadeIn {
  from {
    transform: translateY(-25px);
    z-index: 1100; }

  to {
    transform: translateY(0px);
    z-index: 1; } }


@media only screen and (min-width: 993px) {

  .creator {

    .pizza_builder {
      display: grid;
      grid-template-columns: 33% 17% 17% 33%;
      grid-template-areas: "picker1 preview preview picker2";
      grid-template-rows: auto;
      margin: 5rem 0;

      .ingredients_picker_1 {
        grid-area: picker1; }

      .ingredients_picker_2 {
        grid-area: picker2;
        background-color: orange; }

      .pizza_preview {
        grid-area: preview; } } } }
