/* global.css */
/* Every Sass file has its own .jsx  */

@import url('https://fonts.googleapis.com/css?family=Montserrat:400,500');

* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif; }

::selection {
  background-color: #b3d4fc;
  color: #000;
  text-shadow: none; }

.btn {
  background-color: #454747; }

.btn:hover {
  background-color: #5b5e5e; }

body {
  width: 70vw;
  max-width: 1400px;
  margin: 0 auto;
  overflow: hidden;
  overflow-y: scroll;

  .page_content {
    display: flex;
    justify-content: center;
    align-items: center;
    height: calc(100vh - 64px);
    position: relative;
    top: 64px;

    h2 {
      font-size: 2.5rem; }


    .center {
      display: flex;
      justify-content: center;

      div {
        margin: 0 auto; } } } }

@media only screen and (max-width: 992px) {

  body {
    width: 90vw; }

  .page_content {
    position: relative;
    top: 56px;
    height: calc(100vh - 56px); } }

@media only screen and (max-width: 600px) {

  .page_content {
    position: relative;
    top: 56px                  !important;
    height: calc(100vh - 56px) !important; } }
